import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Translate } from "react-localize-redux";
import Paper from '@material-ui/core/Paper'
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import {CurrentSituationContainer as Container} from '../containers';
import ChartTitle from './ChartTitle';
import { formatDate, formatNumber } from '../utils';

class CurrentSituationPanel extends React.Component {
    componentDidMount() {
        if(!this.props.countryTotals.loaded) {
            this.props.statisticsActions.fetchCountryTotals();
        }
    }

    _formatDate = (date) => { 
        return formatDate(date, this.props.currentLanguage, 'DD MMMM YYYY');
    }

    _renderTile = (lavelTranslationId, value) => {
        return (
            <Grid item xs>
                <Paper>
                    <Box p={2}>
                        <Typography component="p" variant="h6" color="textPrimary" align="center" gutterBottom>
                            <Translate id={lavelTranslationId} />
                        </Typography>
                        <Typography component="p" variant="h5" color="textPrimary" align="center" gutterBottom>
                            {formatNumber(value, this.props.currentLanguage)}
                        </Typography>
                    </Box>
                </Paper>
            </Grid>
        );
    }
      
    render() {
        const { countryTotals, className } = this.props;

        if(!countryTotals.loaded) {
            return (
                <Paper className={className}>
                    <Translate id={"loading"} />
                </Paper>
            );
        }

        const { day, population, numberOfTests, numberOfPositiveCases, numberOfDeaths } = countryTotals.data;

        return (
            <Paper className={className}>
                <ChartTitle>
                    <Translate id="statistics.currentSituationTitle" data={{ date: this._formatDate(day) }}/>
                </ChartTitle>
                <Box pt={0} pr={2} pb={2} pl={2}>
                    <Grid container justify="center" spacing={2} direction="row" alignItems="flex-start">
                        {this._renderTile("statistics.population", population)}
                        {this._renderTile("statistics.totalNumberOfTests", numberOfTests)}
                        {this._renderTile("statistics.totalNumberOfPositiveCases", numberOfPositiveCases)}
                        {this._renderTile("statistics.totalNumberOfDeaths", numberOfDeaths)}
                    </Grid>
                </Box>
            </Paper>
          );
    }
}

CurrentSituationPanel.propTypes = {
    translate: PropTypes.func,
    currentLanguage: PropTypes.string,
    className: PropTypes.string,
    countryTotals: PropTypes.object.isRequired,
    statisticsActions: PropTypes.object
};

export default connect(Container.mapStateToProps, Container.mapDispatchToProps)(CurrentSituationPanel);
